import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import { makeStyles } from '@material-ui/styles'
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';



function ElevationScroll(props) {
    const { children } = props;

    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 0
    });

    return React.cloneElement(children, {
        elevation: trigger ? 4 : 0
    });
}




const useStyles = makeStyles(theme => ({
    toolbarMargin: {
        ...theme.mixins.toolbar
    },
    tabContainer: {
        marginLeft: 'auto'
    },
    tab: {
        ...theme.typography.tab,
    },
    button: {
        textTransform: 'none', 
        borderRadius: '50px',
        marginLeft: '50px', 
        marginRight: '4rem',
        backgroundColor: theme.palette.common.green, 
        "&:hover": {
            backgroundColor: theme.palette.secondary.light
        }
    },
    menu: {
        backgroundColor: theme.palette.common.black,
        borderRadius: "0"
    },
    menuItem: {
        ...theme.typography.tab,
        opacity: 0.7,
        "&:hover": {
            opacity: 1
        }, 
        fontSize: "0.9rem"
    },
    drawerIconContainer: {
        marginLeft: "auto",
        "&:hover": {
            backgroundColor: "transparent",
            color: "#AEAEAF"
        }
    },
    drawerIcon: {
        height: "50px",
        width: "50px", 
        color: "#AEAEAF"
    }, 
    drawer: {
        backgroundColor: theme.palette.common.black,
        fontFamily: "Roboto",
        fontSize: "0.9rem"
    },
    drawerItem: {
        ...theme.typography.tab
    },
    drawerItemLogIn: {
        backgroundColor: theme.palette.common.green
    }
}))
  



export default function Header(props) {

    const classes = useStyles();
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.down("sm"));
    const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);

    const [value, setValue] = useState(0);
    const [anchorEl, setAnchorEl] = useState(null);
    const [openMenu, setOpenMenu] = useState(false);
    const [selectedIndex, setSelectedIndex] = useState(0);
    const [openDrawer, setOpenDrawer] = useState(false);


    const handleChange = (event, newValue) => {
        setValue(newValue)
    };

    const onMouseOver = (event) => {
        setAnchorEl(event.currentTarget);
        setOpenMenu(true)
    };

    const handleClose = (event) => {
        setAnchorEl(null);
        setOpenMenu(false)
    }

    const handleMenuItemClick = (event, index) => {
        setAnchorEl(null);
        setOpenMenu(false);
        setSelectedIndex(index)
    }

    const menuOptions = [ {name: "Quienes Somos", link: "/about"},
                          {name: "Nuestro Equipo", link: "/our-team"}, 
                          {name: "Sala de prensa", link: "/press"}, 
                          {name: "Empleo", link: "/careers"},  
                        ]

                        
    const tabs = (
        <React.Fragment>

            <Tabs value={value} onChange={handleChange} className={classes.tabContainer}>
                                <Tab 
                                    className={ classes.tab}  
                                    component={ Link } to="/" 
                                    label="Home" 
                                />
                                <Tab 
                                    aria-owns={ anchorEl ? "about-menu" : undefined }
                                    aria-haspopup={ anchorEl ? "true" : undefined } 
                                    onMouseOver={ event => onMouseOver(event) }
                                    className={ classes.tab } 
                                    component={ Link }  to="/about" 
                                    label="Quienes Somos"  
                                />
                                <Tab 
                                    className={classes.tab} 
                                    component={Link } to="/program" 
                                    label="Programa"  
                                />
                                <Tab 
                                    className={classes.tab} 
                                    component={ Link } to="/events" 
                                    label="Eventos"
                                />
                                <Tab 
                                    className={classes.tab} 
                                    component={ Link } to="/contact" 
                                    label="Contacto" 
                                />
                            </Tabs>

                            <Button className={ classes.button } variant="contained" >Get Started</Button>

                            <Menu 
                                id="simple-menu" 
                                anchorEl={anchorEl} 
                                open={ openMenu } 
                                onClose={handleClose} 
                                MenuListProps={{onMouseLeave: handleClose}}
                                classes={{ paper: classes.menu }}
                                elevation={0}
                                >   
                                { menuOptions.map((option, index) => (
                                    <MenuItem 
                                        key={ option }
                                        component= { Link } to={ option.link }
                                        classes= {{ root: classes.menuItem }}
                                        onClick={ (event) => { 
                                            handleMenuItemClick(event, index);
                                            setValue(1);
                                            handleClose()
                                            } }
                                        selected={ index === selectedIndex && value === 1 }
                                        >
                                        {option.name}
                                    </MenuItem>
                                ))}
                            </Menu>

        </React.Fragment>
    )


    const drawer = (
        <React.Fragment>
            <SwipeableDrawer 
                disableBackdropTransition={!iOS} disableDiscovery={iOS}
                open={ openDrawer } 
                onClose={ () => setOpenDrawer(false) }
                onOpen={ () => setOpenDrawer(true) }
                classes={{ paper: classes.drawer }}
            > 
                <List disablePadding >
                    <ListItem 
                        divider 
                        button 
                        component={ Link } to="/" 
                        onClick={ () => setOpenDrawer(false) }
                    >
                        <ListItemText className={ classes.drawerItem } disableTypography>Home</ListItemText>
                    </ListItem>
                    <ListItem 
                        divider     
                        button  
                        component={ Link } to="/about" 
                        onClick={ () => setOpenDrawer(false) }
                    >
                        <ListItemText className={ classes.drawerItem } disableTypography>Quienes Somos</ListItemText>
                    </ListItem>
                    <ListItem   
                        divider     
                        button 
                        component={ Link } to="/program" 
                        onClick={ () => setOpenDrawer(false) }
                    >
                        <ListItemText className={ classes.drawerItem } disableTypography>Programa</ListItemText>
                    </ListItem>
                    <ListItem 
                        divider 
                        button 
                        component={ Link } to="/events"
                        onClick={ () => setOpenDrawer(false) } 
                    >
                        <ListItemText className={ classes.drawerItem } disableTypography>Eventos</ListItemText>
                    </ListItem>
                    <ListItem 
                        divider 
                        button 
                        component={ Link } to="/contact" 
                        onClick={ () => setOpenDrawer(false) }
                    >
                        <ListItemText className={ classes.drawerItem } disableTypography>Contacto</ListItemText>
                    </ListItem>
                    <ListItem 
                        divider 
                        button 
                        onClick={ () => setOpenDrawer(false) }
                        className= { classes.drawerItemLogIn }
                    >
                        <ListItemText className={ classes.drawerItem } style={{color: "black"}} disableTypography>Get Started</ListItemText>
                    </ListItem>
                </List>
            </SwipeableDrawer>
            <IconButton 
                onClick={ () => setOpenDrawer(!openDrawer) }
                disableRipple
                className={ classes.drawerIconContainer }
            >
                    <MenuIcon className={ classes.drawerIcon } />
            </IconButton>
        </React.Fragment>
    )


    return (
        <React.Fragment>
                <ElevationScroll>
                    <AppBar position="fixed">
                        <Toolbar>
                            <Typography variant="h5" style={{marginLeft:'2rem'}}>Cuidémonos Juntos</Typography>
                           
                            { matches ? drawer : tabs }

                        </Toolbar>
                    </AppBar>
                </ElevationScroll>
                <div className={classes.toolbarMargin} />
        </React.Fragment>
    )
}